import { Component, ViewChild, Output, EventEmitter, Input } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-book-table',
  templateUrl: './book-table.component.html',
	styleUrls: ['./book-table.component.sass']	
})

export class BookTableComponent {
	
	@ViewChild('bookATabaleForm') 
	bookForm: NgForm;
	
	@Output() 
	isOpenedBookTableForm: EventEmitter<boolean> = new EventEmitter();

	minDate = new Date();
	maxDate = new Date(2020, 0, 1);
	times: string[] = ['7:00AM', '7:30AM', '8:00AM', '8:30AM', '9:00AM', '9:30AM', '10:00AM', '10:30AM', '11:00AM'];
	persons: string[] = ['1 person', '2 persons', '3 persons', '4 persons', '5 persons', '6 persons']
	isValidPhone: boolean = false;
	isOpenedBookForm: boolean = false;
	bookTableFormClass: string = 'openForm';

	constructor(iconRegistry: MatIconRegistry, 
							sanitizer: DomSanitizer) {
		iconRegistry.addSvgIcon(
				'x-mark',
				sanitizer.bypassSecurityTrustResourceUrl('assets/x-mark-thin.svg'));
	}

	closeForm() {
		this.bookTableFormClass = 'closeForm'
		setTimeout(()=>{
			this.isOpenedBookTableForm.emit(false);
		}, 750);
		
	}
	
	isValidPhoneNumber(phoneNo) {
		let phoneRE = /\d\d\d\d\d\d\d\d\d\d/; 
		if(phoneNo.match(phoneRE)){
			this.isValidPhone = false;
		} else {
			this.isValidPhone = true;
		}			
	}

	bookATable() {
		this.isValidPhoneNumber(this.bookForm.value.phone);
		if(this.bookForm.valid && !this.isValidPhone){
			this.bookForm.reset();
			this.closeForm();
		}	
		
	 }

	
}
